.add-action {
	float: right;
}

.card-add {
	vertical-align: text-top;
	overflow-y: hidden;
}

.card-add input, .card-add textarea, .card-add button {
	font-family: "Helvetica Neue";
	background-color: inherit;
	color: #000;
	border: none;
}

.form-inline {
	align-items: center;
}

.card-add button {
	width: auto;
	font-size: 20px;
	float: right;
}

form {
	height: 100%;
}

.card-add input {
	width: 85%;
	height: 20%;
	font-size: 20px;
	font-weight: bold;
}

.card-add textarea {
	font-size: 14px;
	overflow-y: scroll;
	width: 100%;
	height: 86%;
	resize: none;
}